import SockJS from  'sockjs-client';
import  Stomp from 'stompjs';
import store from "../store";
import storage from "./storage";

export function connectionSocket() {
  // console.log(typeof store.state.globalUrl)
  let socket = new SockJS(store.state.globalUrl + '/chat');
  // STOMP
  let stompClient = Stomp.over(socket);
  console.log(store.state)

  // websocket
  stompClient.connect({},() => {

    //channel 
    stompClient.subscribe("/topic/messages/" + store.state.globalUserName, (response) => {
      console.log('')
      console.log(response)
      let obj = JSON.parse(response.body)
      console.log(obj.msgContent);  // msg.body
      let messager = obj.userVO.userName;
      if(!storage.fetch(messager)){
        storage.save(messager, []);
      }else {
        let messages = storage.fetch(messager)
        let msgObj = {
          userName: messager,
          msgType: obj.msgType,
          content: obj.msgContent
        }
        // messages.push(messager+','+obj.msgContent)
        messages.push(msgObj)
        storage.save(messager, messages)
      }
      // store.commit('addMessage', messager+','+obj.msgContent)
    });

    //system 
    stompClient.subscribe("/topic/messages/system" , (frame) => {
      console.log('')
      console.log(frame)
      let result = JSON.parse(frame.body)
      console.log(result)
      if (result.msgType === '12'){
        console.log(result.msgContent, '')
        store.commit('subNewMessager', result.msgContent)
      }else if(result.msgType === '11'){
        console.log(result.msgContent, '')
        store.commit('addNewMessager', result.msgContent)
      }else {
        console.log('')
      }
    });

    //
    // stompClient.subscribe("/topic/messages/GROUP", function(frame){
    //   console.log(frame);
    //   parseOnlineReceive("group", frame)
    // });
    stompClient.subscribe("/topic/messages/GROUP" , (response) => {
      console.log('')
      // console.log(response)
      let obj = JSON.parse(response.body)
      console.log(obj.msgContent);  // msg.body
      let messager = obj.userVO.userName;
      if(!storage.fetch("GROUP")){
        storage.save("GROUP", []);
      }else {
        let messages = storage.fetch("GROUP")
        let msgObj = {
          userName: messager,
          msgType: obj.msgType,
          content: obj.msgContent
        }
        messages.push(msgObj)
        storage.save("GROUP", messages)
      }
      // store.commit('addMessage', messager+','+obj.msgContent)
    });
  store.commit('updateSocket', stompClient)
  });


}




